@use 'hero';
@use 'componets/infos-bar.scss';
@use 'componets/buttons.scss';
@use 'evento.scss';
@use 'sucos.scss';
@use 'refrigerantes.scss';
@use 'footer.scss';

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: roboto, sana-serif;
    color: #fff;
    
}

.container {
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media (max-width: 640px){
        max-width: 80%;
    }

    @media (max-width: 1024){
        max-width: 90%;
    }
}